import React, { Component } from 'react';
import Icon from '../assets/Icon';
import filterPrizes from '../../js/filter-by-prizes';
import celulares from '../../json/celulares';

class PrizesFilters extends Component{

  state = {
    rangeMinValue: 0,
    rangeMaxValue: 3500
  }

  setMinValue = e=>{
    this.setState({
      rangeMinValue: e.target.value
    }) 
  }

  setMaxValue = e=>{
    this.setState({
      rangeMaxValue: e.target.value
    })
  }

  notFound = ()=>{
    const minValue = document.querySelectorAll('input[type="number"]')[0].value,
    maxValue = document.querySelectorAll('input[type="number"]')[1].value;
    let filter;
    celulares.filter(item=> {

      if ( (Number(item.precio) >= Number(maxValue)|| Number(item.precio) <= Number(minValue)) || (Number(item.precio) >= Number(minValue) || Number(item.precio) <= Number(maxValue)) ){
        filter = true;
      } else {
        filter = false;
      }
    });
    return filter;
  }

  render(){
    return(
      <div className="prizes">
        <h4>Precio</h4>
        <span>Fijar precio máximo:  
          <input type="range" id="price-max" min="0" max="3500" value={this.state.rangeMaxValue} onChange={this.setMaxValue}/>  
        </span>

        <span>Fijar precio mínimo:  
          <input type="range" id="price-min" min="0" max="3500" value={this.state.rangeMinValue} onChange={this.setMinValue} />  
        </span>

        <span className="range-prize">
          <input type="number" min="0" value={this.state.rangeMinValue} htmlFor="price" disabled />&nbsp;&nbsp;-&nbsp;&nbsp;
          <input type="number" min="0" value={this.state.rangeMaxValue} disabled />
          <button id="set-range-prize" onClick={ () => {
            this.props.searchByPrizes(filterPrizes(), this.notFound());
            this.props.changeTextNotFound("No se ha encontrado ningún producto con ese precio...") }
            }>
            <Icon iconName="fas fa-chevron-right" iconStyle={{padding: "0"}} />
          </button>
        </span>
      </div>
    )
  }
}

export default PrizesFilters;