import celulares from '../json/celulares.json';

const filterPrizes = () =>{
  const minValue = document.querySelectorAll('input[type="number"]')[0].value,
  maxValue = document.querySelectorAll('input[type="number"]')[1].value;
  return celulares.filter(item => {
    if ( (Number(maxValue) >= Number(item.precio) && Number(item.precio) >= Number(minValue)) || (Number(maxValue) <= Number(item.precio) && Number(item.precio) <= Number(minValue)) ){
      return item;
    }
  });
}

export default filterPrizes;